const LucidSimGallery = ({id, hidden, videoSets}) => {
    const [currentSet, setCurrentSet] = useState(videoSets[0]);
    const [playbackTime, setPlaybackTime] = useState(0);
    const [maxDuration, setMaxDuration] = useState(0);
    const [renderKey, setRenderKey] = useState(Date.now());
    const [isPlaying, setIsPlaying] = useState(false);
    const videoRef1 = useRef(null);
    const videoRef2 = useRef(null);

    const updateVideoSettings = (videoElement) => {
        if (videoElement) {
            const duration = videoElement.duration;
            setMaxDuration(duration);
            setPlaybackTime(0);
            videoElement.currentTime = 0;
        }
    };

    const handleSliderChange = event => {
        const newTime = parseFloat(event.target.value);
        setPlaybackTime(newTime);
        if (videoRef1.current && videoRef2.current) {
            videoRef1.current.currentTime = newTime;
            videoRef2.current.currentTime = newTime;
        }
    };

    const handlePlayPause = () => {
        if (videoRef1.current && videoRef2.current) {
            if (isPlaying) {
                videoRef1.current.pause();
                videoRef2.current.pause();
            } else {
                videoRef1.current.play();
                videoRef2.current.play();
            }
            setIsPlaying(!isPlaying);
        }
    };

    useEffect(() => {
        if (videoRef1.current && videoRef2.current) {
            videoRef1.current.currentTime = playbackTime;
            videoRef2.current.currentTime = playbackTime;
        }
    }, [renderKey]);

    const handleSetChange = set => {
        setCurrentSet(set);
        setIsPlaying(false);
        setRenderKey(Date.now());
    };

    return (
        <div id={id} className={`flex flex-col ${hidden ? 'hidden' : ''}`}>
            <div className="flex justify-around items-center">
                <video key={renderKey} ref={videoRef1} muted playsInline loop className="w-1/2"
                       onLoadedMetadata={(e) => updateVideoSettings(e.target)}>
                    <source src={currentSet.video1} type="video/mp4"/>
                </video>
                <video key={renderKey + 1} ref={videoRef2} muted playsInline loop className="w-1/2"
                       onLoadedMetadata={(e) => updateVideoSettings(e.target)}>
                    <source src={currentSet.video2} type="video/mp4"/>
                </video>
            </div>
            <div className="flex items-center w-full mt-2">
                <input type="range" min="0" max={maxDuration} value={playbackTime} step="0.01"
                       onChange={handleSliderChange} className="w-full"/>
                <button onClick={handlePlayPause}
                        className="ml-4 px-3 py-1 bg-white text-gray-800 rounded-lg shadow focus:outline-none">
                    {isPlaying ? 'Pause' : 'Play'}
                </button>
            </div>
            <div className="flex justify-center items-center overflow-x-auto mb-4 gap-4">
                {videoSets.map(set => (
                    <img key={set.id} src={set.image} alt="Video Set"
                         onClick={() => handleSetChange(set)}
                         className={`cursor-pointer p-2 rounded-lg transition-all duration-300 ease-in-out ${currentSet.id === set.id ? 'scale-95 shadow-inner' : 'shadow-md hover:scale-105 hover:shadow-2xl'}`}
                         style={{width: '240px', height: '240px', objectFit: 'contain'}}
                    />
                ))}
            </div>
        </div>
    );
};


